<script lang="ts" setup>
import { ref, onMounted } from "vue";
import { useContactStore } from "@/stores/contactStore";
import { useToast } from "primevue/usetoast";
import { useConfirm } from "primevue/useconfirm";
import DataTable from "primevue/datatable";
import Column from "primevue/column";
import Button from "primevue/button";
import ProgressSpinner from "primevue/progressspinner";
import ConfirmDialog from "primevue/confirmdialog";
import ContactModal from "@/components/widgets/ContactModal.vue";
import type { PersonContact } from "@/types/models";

const contactStore = useContactStore();
const toast = useToast();
const confirm = useConfirm();
const isLoading = ref(false);
const error = ref<string | null>(null);
const showModal = ref(false);
const isEditMode = ref(false);
const selectedContact = ref<PersonContact | null>(null);

const columns = [
  { field: "fullName", header: "" },
  { field: "position", header: "" },
  { field: "phone", header: "" },
  { field: "email", header: "Email" },
  { field: "organization.displayValue", header: "" },
  { header: "" },
];

onMounted(async () => {
  isLoading.value = true;
  error.value = null;
  try {
    await contactStore.fetchAll();
  } catch (err) {
    error.value =
      "   : " +
      (err instanceof Error ? err.message : " ");
    toast.add({
      severity: "error",
      summary: "",
      detail: error.value,
      life: 3000,
    });
  } finally {
    isLoading.value = false;
  }
});

function openCreateModal() {
  selectedContact.value = {
    id: "",
    fullName: "",
    position: "",
    phone: "",
    email: "",
    organization: undefined,
  };
  isEditMode.value = false;
  showModal.value = true;
}

function openEditModal(contact: PersonContact) {
  selectedContact.value = { ...contact };
  isEditMode.value = true;
  showModal.value = true;
}

function closeModal() {
  showModal.value = false;
  selectedContact.value = null;
}

async function saveContact(contact: PersonContact) {
  try {
    const contactData = {
      fullName: contact.fullName,
      position: contact.position,
      phone: contact.phone,
      email: contact.email,
      organizationId: contact.organization?.id,
    };
    if (isEditMode.value) {
      await contactStore.update(contact.id, contactData);
      toast.add({
        severity: "success",
        summary: "",
        detail: " ",
        life: 3000,
      });
    } else {
      await contactStore.create(contactData);
      toast.add({
        severity: "success",
        summary: "",
        detail: " ",
        life: 3000,
      });
    }
  } catch (err) {
    toast.add({
      severity: "error",
      summary: "",
      detail:
        "   : " +
        (err instanceof Error ? err.message : " "),
      life: 3000,
    });
  } finally {
    showModal.value = false;
    selectedContact.value = null;
  }
}

function confirmDelete(id: string) {
  confirm.require({
    message: " ,     ?",
    header: " ",
    icon: "pi pi-exclamation-triangle",
    acceptLabel: "",
    rejectLabel: "",
    accept: async () => {
      try {
        await contactStore.remove(id);
        toast.add({
          severity: "success",
          summary: "",
          detail: " ",
          life: 3000,
        });
      } catch (err) {
        toast.add({
          severity: "error",
          summary: "",
          detail:
            "   : " +
            (err instanceof Error ? err.message : " "),
          life: 3000,
        });
      }
    },
  });
}
</script>

<template>
  <div class="contacts-tab">
    <ProgressSpinner v-if="isLoading" class="loader" />
    <div v-else-if="error" class="error">{{ error }}</div>
    <div v-else>
      <div class="actions-container">
        <Button
          label=" "
          icon="pi pi-plus"
          class="p-button-lg"
          @click="openCreateModal"
        />
      </div>
      <DataTable
        v-if="contactStore.contacts?.length"
        :value="contactStore.contacts"
        :columns="columns"
        row-key="id"
        paginator
        :rows="10"
        class="contacts-table"
        scrollable
      >
        <Column field="fullName" header="" />
        <Column field="position" header="" />
        <Column field="phone" header="" />
        <Column field="email" header="Email" />
        <Column field="organization.displayValue" header="" />
        <Column header="">
          <template #body="{ data }">
            <div class="actions-container">
              <Button
                icon="pi pi-pencil"
                severity="warning"
                class="p-button-sm"
                @click.stop="openEditModal(data)"
              />
              <Button
                icon="pi pi-trash"
                severity="danger"
                class="p-button-sm"
                @click.stop="confirmDelete(data.id)"
              />
            </div>
          </template>
        </Column>
      </DataTable>
      <div v-else class="no-data">
        <p> </p>
        <Button
          label="  "
          icon="pi pi-plus"
          class="p-button-lg"
          @click="openCreateModal"
        />
      </div>
      <ContactModal
        v-if="showModal"
        :visible="showModal"
        :contact="selectedContact"
        :isEdit="isEditMode"
        @save="saveContact"
        @cancel="closeModal"
      />
      <ConfirmDialog />
    </div>
  </div>
</template>
